.grid-container {
  display: grid;
  grid-template-columns: 2fr 1fr 1fr 2fr 2fr 1fr 1fr 2fr;
  grid-column-gap: 2vw;
  grid-row-gap: 2vh;
}

header {
  grid-column: 1/8;
  grid-row: 1;
  padding-left: 4vw;
}

main {
  grid-column: 2/8;
  grid-row: 3;
}

.byline {
  text-transform: uppercase;
  font-size: 85%;
}

.clear {
  display: flow-root;
}

em strong,
strong em {
  color: #bb4444;
}

a:link {
  color: #555577;
  text-decoration: underline;
}

a:visited {
  color: #555577;
  text-decoration: underline;
}

a:hover {
  color: #bb4444;
}

h3 {
  color: #663399;
}

h3:nth-child(2n+1) {
  transform: rotate(-3deg);
  margin-bottom: 2em;
}
